<script setup>

    import {ref, onMounted} from 'vue';

    const shownTip = ref("");

    let learnGraph_elm;
    let selfGraph_elm;
    // let tooltipElm;

    let skillScroll_flag = true;

    onMounted(() => {
        learnGraph_elm = document.getElementsByClassName("skill__learnGraph");
        selfGraph_elm = document.getElementsByClassName("skill__selfGraph");

        // tooltipElm = document.getElementsByClassName("skill__tool__toolTip");    
        learnGraph_elm = document.getElementsByClassName("skill__learnGraph");

        window.addEventListener("click", () => {
            shownTip.value = "";
        });
        


        window.addEventListener("scroll", () => {
            
            if(skillScroll_flag == false) {
                return;
            }
            skillScroll_flag = false;
            let timeOut = setTimeout(() => {
                skillScroll_flag = true;
                clearTimeout(timeOut);
            }, 100);

            let screen_hei = document.documentElement.clientHeight;
            let scroll_y = window.scrollY;

            for(let i = 0; i < learnGraph_elm.length; i++) {
                let learn_top = learnGraph_elm[i].offsetTop;
                if(learn_top - scroll_y <= -0.3 * screen_hei) {
                    learnGraph_elm[i].style.maxWidth = 100 + "%";
                    selfGraph_elm[i].style.maxWidth = 100 + "%";
                }

            }

        });

    });

    // let setTip = ((ev) => {

    //     let targetElm = ev.target;
    //     let target_left = targetElm.offsetLeft;
    //     target_left += 10;
    //     tooltipElm[0].style.left = target_left + "px";

    // });

</script>

