<template>
    <header class="menu">
        <div class="menu__wrapper">
            <a href="#top" class="menu__item" :class="{menu__HL: cur_view=='top'}">
                Top
            </a>
            <a href="#profile" class="menu__item" :class="{menu__HL: cur_view=='profile'}">
                Profile
            </a>
            <a href="#skill" class="menu__item" :class="{menu__HL: cur_view=='skill'}">
                Skill
            </a>
            <a href="#works" class="menu__item" :class="{menu__HL: cur_view=='works'}">
                Works
            </a>
        </div>
    </header>
</template>

<script setup>

    import { ref, onMounted } from 'vue';

    const cur_view = ref("top");

    let headScroll_flag = true;

    // let top_elm;
    let profile_elm;
    let skill_elm;
    let works_elm;

    onMounted(() => {
        // top_elm = document.getElementById("top");
        profile_elm = document.getElementById("profile");
        skill_elm = document.getElementById("skill");
        works_elm = document.getElementById("works");
    });
    
    window.addEventListener("scroll", () => {
        if(headScroll_flag == false) {
            return;
        }

        headScroll_flag = false;
        let timeOut = setTimeout(() => {
            headScroll_flag = true;
            clearTimeout(timeOut);
        }, 100);

        // let top_y = top_elm.getBoundingClientRect().top;
        let profile_y = profile_elm.getBoundingClientRect().top;
        let skill_y = skill_elm.getBoundingClientRect().top;
        let works_y = works_elm.getBoundingClientRect().top;

        cur_view.value = "top"

        if(profile_y <= 200) {
            cur_view.value = "profile";
        }
        if(skill_y <= 200) {
            cur_view.value = "skill";
        }
        if(works_y <= 200) {
            cur_view.value = "works";
        }
    })

    

</script>

<style>
/* -------------------------------------------- */
/* FOR MOBILE FIRST */
/* ---------------------------------------------*/

    .menu {
        position: fixed;
        top: 0px;
        z-index: 100;
    }

    .menu__wrapper {
        width: 100VW;
        background-color: rgb(220, 220, 220);
        display: flex;
        justify-content: space-around;
    }
    
    .menu__item {
        width: 24%;
        height: 100%;
        text-align: center;
        padding: 0.7rem 0;
        text-decoration: none;
        /* font-size: 1.2rem; */
        font-weight: bold;
        color: black;
        display: block;
    }

    .menu__HL {
        color: whitesmoke;
        text-shadow: 1px 1px black;
        transition-delay: 200ms;
        transition-duration: 500ms;
    }

    /* -------------------------------------------- */
    /* FOR TABLET */
    /* -------------------------------------------- */
    @media screen and (min-width:768px) and ( max-width:1024px) {
        /* .menu__wrapper {
            justify-content: left;
        }
        
        .menu__item {
            width: 10rem;
        } */
    }

    /* -------------------------------------------- */
    /* FOR PC */
    /* -------------------------------------------- */
    @media screen and (min-width:1024px) {
        .menu__wrapper {
            width: 15vw;
            max-width: 20rem;
            height: 100vh;
            justify-content: initial;
            flex-direction: column;
            padding-top: 3rem;
            padding-left: 2rem;
        }
        .menu__item {
            width: 80%;
            height: fit-content;
            text-align: initial;
        }
    }
</style>
